import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import moment from 'moment';

Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    spends: [],
    allCategories: [],
  },
  getters: {
    monthlySpend: (state) => state.spends
      .filter(x => moment(x.date).isSame(moment(), 'M'))
      .reduce((prev, curr) => prev + curr.amount, 0),
    todaySpend: (state) => state.spends
      .filter(x => moment(x.date).isSame(moment().subtract(1, 'days'), 'd'))
      .reduce((prev, curr) => prev + curr.amount, 0),
    categories: (state) => [...state.allCategories, ...state.spends.map((x) => x.category)]
      .reduce((prev, curr) => [...prev, ...(prev.includes(curr) ? [] : [curr])], []),
    dayComparisionData: (state) => {
      moment.locale('es');
      const daysToCheck = 5;
      const labels = [...Array(daysToCheck).keys()]
        .map(i => moment().subtract(i, 'days').from(moment()))
        .reverse();
      const data = [...Array(daysToCheck).keys()]
        .map(i => state.spends.reduce((prev, curr) => prev + (moment(curr.date).isSame(moment().subtract(i, 'days').startOf('day'), 'd') ? curr.amount : 0), 0))
        .reverse();
      const backgroundColor = [...Array(daysToCheck).keys()]
        .map(i => `rgba(29, 99, 204, 1)`);
      return {
          labels,
          datasets: [
            {
              label: 'Gasto',
              backgroundColor,
              data,
            },
          ]
        };
    },
  },
  mutations: {
    addSpend: (state, payload) => {
      const sortedArray = [...state.spends, payload].sort((a,b) => new moment(b.date).format('YYYYMMDD') - new moment(a.date).format('YYYYMMDD'));
      state.spends = sortedArray;
    },
    saveInitData: (state, payload) => {
      state.spends = payload;
    },
  },
  actions: {
    login: ({ commit },) => {
      return axios.get(
        'https://users.dcc.uchile.cl/~sblasco/wallet/')
        .then(({ data }) => {
          const sortedArray = data.sort((a,b) => new moment(b.date).format('YYYYMMDD') - new moment(a.date).format('YYYYMMDD'));
          commit('saveInitData', sortedArray);
          return Promise.resolve();
        });
    },
    addSpend: ({ state, commit }, payload) => {
      commit('addSpend', payload);
      return axios.post('https://users.dcc.uchile.cl/~sblasco/wallet/', state.spends)
        .then(({ data }) => {
          const sortedArray = data.sort((a,b) => new moment(b.date).format('YYYYMMDD') - new moment(a.date).format('YYYYMMDD'));
          commit('saveInitData', sortedArray);
          return Promise.resolve();
        });
    },
  },
  modules: {
  },
});
